.navbar-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: end;
      align-items: flex-end;
  margin: 20px;
  list-style: none;
}


.navbar-item:before {
  content: '\232a';
  margin-right: 5px;
}

.navbar-item {
  background: transparent;
  padding: 10px;
  text-align: left;
  width: 400px;
  color: rgb(56, 165, 5);
}


.navbar-item:hover {
  cursor: crosshair;
  background: rgb(56, 165, 5);
  color: black;
}


@media screen and (max-width: 400px) {

 .navbar-container {
   -ms-flex-pack: center;
       justify-content: center;
   -ms-flex-align: center;
       align-items: center;
 }

 

}


